import { Component, OnInit } from '@angular/core';
import { AngularFire, AuthProviders, AuthMethods, FirebaseListObservable } from 'angularfire2';

import { HeroService } from '../heroes/hero.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  constructor(private af: AngularFire,
    public heroService: HeroService) {
   }

  ngOnInit() {
    this.af.auth.subscribe(authState => {
      if (!authState) {
        console.log('NOT LOGGED IN');
        this.heroService.displayName = null;
        this.heroService.photoURL = null;
        return;
      } 
      console.log('LOGGED IN', authState);
      this.heroService.displayName = authState.auth.displayName;
      this.heroService.photoURL = authState.auth.photoURL;
      // this.af.database.object('/users/' + authState.uid).update({
      //   uid: authState.facebook.uid,
      //   displayName: authState.facebook.displayName,
      //   email: authState.facebook.email,
      //   photoURL: authState.facebook.photoURL,
      // });
    });
  }

  register() {
    console.log('register');
    this.af.auth.createUser({
      email: 'vonniekinsey2@gmail.com',
      password: '!@#$12345'
    })
    .then(authState => {
    console.log('REGISTER-THEN', authState)
    })
    .catch(error => console.log('REGISTER-ERROR', error));
  }

  login() {
    this.af.auth.login({
      provider: AuthProviders.Facebook,
      method: AuthMethods.Popup,
      scope: ['user_website', 'user_photos']
    }).then((authState: any) => {
      console.log('AFTER LOGIN', authState);
      console.log(authState.facebook);
      this.af.database.object('/users/' + authState.uid).update({

        accessToken: authState.facebook.accessToken,
        // uid: authState.facebook.uid,
        // displayName: authState.facebook.displayName,
        // email: authState.facebook.email,
      })
    });
  }

  logout() {
    console.log('logout request');
    this.af.auth.logout();
  }

}



